<template>
  <div>
    <h1>Images Upload and Download</h1>
    <div class="form-group">
      <label for="fileInput">Select Image:</label>
      <input type="file" id="fileInput" @change="handleFileChange">
      <button class="btn btn-primary" :class="{ 'btn-clicked': isButtonClicked }" @click="uploadImage">Upload</button>
      <p v-if="errorMessage" class="error-message">{{ errorMessage }}</p>
    </div>
    <div>
      <table>
        <thead>
          <tr>
            <th>ID</th>
            <th>Thumbnail</th>
            <th>UploadTime</th>
            <th>Action</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="image in images" :key="image.id">
            <td>{{ image.id }}</td>
            <td>
              <img :src="getImageUrl(image.id)" alt="Image" width="200" height="200">
            </td>
            <td>{{ image.upload_time }}</td>
            <td>
              <button class="btn btn-outline-dark0" :class="{ 'btn-clicked': isButtonClicked }" @click="downloadImage(image.id)">Save</button>
              <button class="btn btn-outline-dark1" :class="{ 'btn-clicked': isButtonClicked }" @click="confirmDelete(image.id)">Delete</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      images: [],
      selectedFile: null,
      selectedImageId: null,
      errorMessage: '',
      isButtonClicked: false,
    };
  },
  created() {
    this.getImages();
  },
  methods: {
    getImages() {
      this.$http.get('http://192.168.242.106:32680').then(
        (response) => {
          this.images = response.body;
        },
        (error) => {
          console.error(error);
        }
      );
    },
    downloadImage(imageId) {
      this.isButtonClicked = true;
      const link = document.createElement('a');
      link.style.display = 'none';
      const downloadUrl = `http://192.168.242.106:32680/${imageId}`;
      link.href = downloadUrl;
      link.setAttribute('attachment_filename', `image_${imageId}.jpg`);
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      this.isButtonClicked = false;
    },
    handleFileChange(event) {
      this.selectedFile = event.target.files[0];
    },
    uploadImage() {
      if (this.selectedFile) {
        this.isButtonClicked = true;
        const formData = new FormData();
        formData.append('image', this.selectedFile);

        this.$http.post('http://192.168.242.106:32680', formData).then(
          (response) => {
            console.log(response.status);
            if (response.status == 201) {
              this.getImages();
              this.selectedFile = null;
              this.errorMessage = '';
            }
            this.isButtonClicked = false;
          },
          (error) => {
            console.error(error.status);
            if (error.status == 406) {
              this.errorMessage = 'Invalid Image Types!';
            }
            this.isButtonClicked = false;
          }
        );
      }
    },
    getImageUrl(imageId) {
      return `http://192.168.242.106:32680/${imageId}`;
    },
    confirmDelete(id) {
      if (confirm('Are you sure you want to delete this image?')) {
        this.deleteImage(id);
      }
    },
    deleteImage(id) {
      this.isButtonClicked = true;
      this.$http.delete('http://192.168.242.106:32680/' + id).then(
        (response) => {
          this.getImages();
          this.isButtonClicked = false;
        },
        (error) => {
          console.error(error);
          this.isButtonClicked = false;
        }
      );
    },
  },
};
</script>

<style>
/* 样式定义 */
body {
  font-family: Arial, sans-serif;
  background-color: #f2f2f2;
  margin: 0;
  padding: 0;
}

h1 {
  text-align: center;
  color: #333;
  margin-top: 20px;
}

.form-group {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

label {
  font-weight: bold;
  margin-right: 10px;
}

input[type="file"] {
  margin-right: 10px;
}

.btn {
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.btn-primary {
  background-color: #4caf50;
  color: #fff;
}

.btn-outline-dark0 {
  background-color: #4caf50;
  color: #fff;
  margin-right: 5px;
}

.btn-outline-dark1 {
  background-color: #f44336;
  color: #fff;
  margin-right: 5px;
}

.btn-clicked {
  background-color: gray !important;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

th,
td {
  padding: 10px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

img {
  max-width: 200px;
  max-height: 200px;
}
</style>
